.container {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.wrapper {
  width: 500px;
  margin-top: 200px;
}

.wrapper h1 {
  font-size: 24px;
  font-weight: bold;
  line-height: 1.42;
  letter-spacing: -0.25px;
  text-align: center;
  color: rgb(51, 51, 51);
  margin-bottom: 24px;
}

.from_wrap label {
  font-size: 14px;
  margin-bottom: 10px;
  display: inline-block;
  font-weight: 600;
}

.input_box {
  position: relative;
}

.user_id .input_icon {
  position: absolute;
  left: 11px;
  top: 37%;
}

.user_password .input_icon {
  position: absolute;
  left: 11px;
  top: 30%;
}

.input_box input {
  border: 1px solid rgb(222, 222, 222);
  padding: 15px 8px 15px 38px;
  font-size: 14px;
  border-radius: 5px;
  display: block;
  width: 100%;
  letter-spacing: -0.25px;
  font-weight: 400;
  color: rgb(51, 51, 51);
  box-sizing: border-box;
}

.input_box .input_error {
  border: 1px solid rgb(240, 55, 56);
}

.error_wrap {
  position: absolute;
  right: 0;
  top: 0;
  width: 40px;
  height: 40px;
  display: none;
  align-items: center;
  justify-content: center;
}

.on {
  display: flex;
}

.error_wrap svg {
  display: block;
  margin-top: 10px;
}

.error_txt {
  position: absolute;
  right: 0;
  top: 50px;
  background-color: #000;
  color: rgb(255, 255, 255);
  padding: 10px;
  border-radius: 5px;
  white-space: nowrap;
  z-index: 1;
  display: none;
}

.in {
  display: inline-block;
}

.error_wrap .error_txt::before {
  content: "";
  display: block;
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-bottom: 13px solid #000;
  position: absolute;
  top: -9px;
  right: 10px;
}

.form_cont .user_password {
  margin-top: 25px;
}

.submit_cont {
  margin-top: 40px;
}

.login_btn {
  display: block;
  width: 100%;
  height: 45px;
  line-height: 45px;
  text-align: center;
  border-radius: 5px;
  background-color: rgb(2, 78, 238);
  color: rgb(255, 255, 255);
  border: none;
  cursor: pointer;
  font-size: 16px;
  font-weight: 600;
}

.main_signup_btn {
  display: block;
  width: 100%;
  height: 45px;
  line-height: 45px;
  text-align: center;
  border-radius: 5px;
  margin-top: 15px;
  color: rgb(2, 78, 238);
  font-size: 16px;
  font-weight: 600;
}

.main_signup_btn:hover {
  background-color: rgb(241, 244, 255);
}